$rotation: 40deg;

.navigation {
  height: 11vh;
  width: 100vw;
  position: fixed;
  background: #444;
  top: 0; left: 0; right: 0;
  z-index: 2;
  @include transform-origin(top left);
  @include transform(rotate(-$rotation));
  @include transition(all 0.3s ease-in-out);

  &__logo {
    display: inline-block;
    border: 0;
    width: 3vw;
    margin-left: .5vw;
    margin-top: 5vh;

    &:hover {
      text-decoration: none;
      border: 0;
    }

    &--image {
      display: block;
      width: 100%;
      @include transform-origin(50%);
      @include transform(rotate($rotation));
    }
  }
}
