.index {
  position: relative;

  &,
  &__snow,
  &__wildlife {
    height: 100%;
    margin: 0;
  }

  &__snow,
  &__wildlife {
    display: inline-block;
    width: 50%;
    background-size: cover;
    background-position: bottom;
  }

  &__snow {
    background-image: url(/images/index/snow.jpg);
  }

  &__wildlife {
    background-image: url(/images/index/wildlife.jpg);
    background-position: 0 70%;
  }

  &__header_one {
    position: absolute;
    top: 50%;
    left: 50%;
    @include transform(translate(-50%, -50%));
  }
}
